
@use "@/assets/scss/_base";

:deep(.main) {
    display: flex;
    min-width: calc(v-bind("moduleConfig.mainLayer.noAirplanesMessage.width") * 1px);
    min-height: 300px;
    align-items: center;
    justify-content: center;
}

.chart {
    min-width: 90vw;
    min-width: 90svw;
    min-height: 80vh;
    min-height: 80svh;
}

.no-airplane-message {
    color: v-bind("moduleConfig.mainLayer.noAirplanesMessage.fill");
}

.fps {
    position: fixed;
    z-index: 1;
    top: 0;
    left: 0;
    background: v-bind("moduleConfig.stageBackgroundColor");

    @include base.column-padding;
}
